.register {
    margin: auto;
    text-align: center;
    width: 300px;
    text-shadow: black 2px 2px 3px;
    color: snow;
    font-family: 'Berkshire Swash', cursive;
  }
  
  .register button,
  .register input {
    width: 174px;
    border: none;
    padding: 10px;
    border-radius: 5%;
    background-color: lightgray;
    box-shadow: black 1px 2px 3px;
    color: black;
    opacity: 80%;
  }
  
  
  .register button:hover,
  .register input:hover {
    background-color: rgb(35, 207, 199);
  }
  .register a:hover{
      color: rgb(35, 207, 199);
  }
  .register h4 {
    color: white;
    text-shadow: black 2px 3px 5px;
  }
  
  .register a {
    color: white;
    text-shadow: black 2px 3px 5px;
  }
  
  /* imports */
  @import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap');