.newVacation input,
.newVacation textarea {
  border: 1px solid black;
  border-radius: 5%;
  margin: 5px;
  width: 250px;
  padding: 5px;
  box-shadow: gray 1px 2px 3px;
  word-wrap: break-word;
  opacity: 80%;
}

div label {
  font-family: "Berkshire Swash";
  color: white;
  text-shadow: black 1px 2px 3px;
}

h2 {
  font-family: "Berkshire Swash";
  color: white;
  text-shadow: black 1px 2px 3px;
}

div textarea {
  height: 150px;
}

div textarea:focus,
div input:focus {
  border: 0;
  outline: none;
  opacity: 100%;
}

div input {
  height: 50px;
}

div .back {
  margin-top: 15px;
}

.back {
  margin-top: 20px;
  font-size: x-large;
  font-family: "Berkshire Swash";
  text-shadow: black 1px 2px 3px;
}