.userVacations>.vacations {
  margin: 15px;
  padding: 10px;
  display: inline-block;
  border: 1px solid black;
  word-wrap: break-word;
  width: 370px;
  height: 930px;
  color: black;
  overflow: hidden;
  text-shadow: snow 2px 1px 2px;
  font-family: "Berkshire Swash", cursive;
  background-color: white;
}

.vacations>img {
  text-align: center;
  border: 1px solid black;
  width: 350px;
  height: 200px;
}

.vacations>img:hover{
  opacity: 90%;
}

.vacations>h4 {
  text-align: left;
}

.user>h4 {
  font-size: large;
  text-decoration: none;
  color: snow;
  text-shadow: black 3px 2px 5px;
  font-family: "Berkshire Swash";
  margin: 0px 20px;
}

.vacations>hr {
  width: 300px;
}

.userVacations {
  color: white;
  text-shadow: black 2px 1px 2px;
  font-family: "Berkshire Swash";
}

.user {
  display: flex;
  margin: 20px 80px;
  justify-content: flex-end;
}

.description {
  height: 200px;
  word-wrap: break-word;
  overflow: auto;
}
