.adminPage>.vacations {
  margin: 15px;
  padding: 10px;
  display: inline-block;
  border: 1px solid black;
  word-wrap: break-word;
  width: 370px;
  height: 825px;
  color: black;
  overflow: hidden;
  text-shadow: snow 2px 1px 2px;
  font-family: "Berkshire Swash", cursive;
  background-color: white;
}

.vacations>img {
  margin-top: 10px;
  text-align: center;
  border: 1px solid black;
  width: 350px;
  height: 200px;
}

.vacations>img:hover {
opacity: 90%;
}

.vacations>h4 {
  text-align: left;
  font-size: 16px;
  line-height: 20px;
  margin: 20px;
}

.user>h4 {
  font-size: large;
  text-decoration: none;
  color: snow;
  text-shadow: black 3px 2px 5px;
  font-family: "Berkshire Swash";
  margin: 0px 20px;
}

.vacations>hr {
  width: 300px;
}

.adminPage {
  color: white;
  text-shadow: black 2px 1px 2px;
  font-family: "Berkshire Swash";
}

.user {
  display: flex;
  margin: 20px 80px;
  justify-content: flex-end;
}

.description {
  height: 240px;
  word-wrap: break-word;
  overflow: auto;
}

.vacations>.changeButtonsEdit {
  border: none;
  background-color: none;
  display: inline-block;
  margin-right: 310px;
}

.vacations>.changeButtonsX {
  border: none;
  background-color: none;
  display: inline-block;
}

.vacations>label {
  text-align: center;
}


.newVacationBtn .new {
  text-align: center;
  width: 50px;
  font-size: x-large;
  border-radius: 50%;
}

.newVacationBtn {
  display: block;
  position: absolute;
  top: 209px;
  right: 330px;
  opacity: 90%;
}
.newVacationBtn:hover {
  opacity: 100%;
}

.graph {
  display: block;
  position: absolute;
  top: 205px;
  right: 410px;
  border-radius: 20%;
  opacity: 80%;
}
.graph img{
  border-radius: 30%;
  width: 70px;
}

.graph:hover{
  opacity: 100%;
}

.editVacation label {
  color: black;
  text-shadow: wheat 2px 1px 2px;
}

.editVacation input,
.editVacation textarea {
  border: 1px solid black;
  border-radius: 5%;
  margin: 5px;
  width: 215px;
  padding: 5px;
  box-shadow: gray 1px 2px 3px;
  word-wrap: break-word;
  opacity: 80%;
}