.layout {
  text-align: center;
  margin: 0;
  padding: 0;
}

.layout>hr {
  width: 500px;
  border: lightpink solid 0.5px;
  border-radius: 50%;
  opacity: 50%;
}

.layout>h1 {
  color: snow;
  text-shadow: black 3px 2px 5px;
  font-size: 50px;
  margin-top: 60px;
  font-family: "Berkshire Swash", cursive;
}

.user>a {
  font-size: large;
  text-decoration: none;
  color: snow;
  text-shadow: black 3px 2px 5px;
  font-family: "Berkshire Swash", cursive;
  margin: 0 10px;
}

.user>a:hover {
  color: rgb(35, 207, 199);
}