@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
body {
  text-align: center;
  background-image: url("/images/back-ground-image.jpg");
  background-repeat: repeat-y;
  background-size: cover;
  height: 100%;
}

hr {
  width: 500px;
  border: lightpink solid 1px;
  border-radius: 50%;
  opacity: 50%;
}
.layout {
  text-align: center;
  margin: 0;
  padding: 0;
}

.layout>hr {
  width: 500px;
  border: lightpink solid 0.5px;
  border-radius: 50%;
  opacity: 50%;
}

.layout>h1 {
  color: snow;
  text-shadow: black 3px 2px 5px;
  font-size: 50px;
  margin-top: 60px;
  font-family: "Berkshire Swash", cursive;
}

.user>a {
  font-size: large;
  text-decoration: none;
  color: snow;
  text-shadow: black 3px 2px 5px;
  font-family: "Berkshire Swash", cursive;
  margin: 0 10px;
}

.user>a:hover {
  color: rgb(35, 207, 199);
}
.loginPage {
  margin: auto;
  margin-top: 122px;
  text-align: center;
  width: 300px;
  text-shadow: black 2px 2px 3px;
  color: snow;
  font-family: 'Berkshire Swash', cursive;
}

.loginPage button,
.loginPage input {
  width: 174px;
  border: none;
  padding: 10px;
  border-radius: 5%;
  background-color: lightgray;
  box-shadow: black 1px 2px 3px;
  color: black;
  opacity: 80%;
}

.loginPage a {
  color: white;
  display: inline-block;
  margin: 0;
  text-shadow: black 2px 2px 3px;
}

.loginPage button:hover,
.loginPage input:hover {
  background-color: rgb(35, 207, 199);
}
.loginPage a:hover{
    color: rgb(35, 207, 199);
  }
.loginPage h4 {
  color: white;
  text-shadow: black 2px 3px 5px;
  padding: 5px;
}

.loginPage button:focus,
.loginPage input:focus {
  border: 0;
  outline: none;
}

/* imports */
.register {
    margin: auto;
    text-align: center;
    width: 300px;
    text-shadow: black 2px 2px 3px;
    color: snow;
    font-family: 'Berkshire Swash', cursive;
  }
  
  .register button,
  .register input {
    width: 174px;
    border: none;
    padding: 10px;
    border-radius: 5%;
    background-color: lightgray;
    box-shadow: black 1px 2px 3px;
    color: black;
    opacity: 80%;
  }
  
  
  .register button:hover,
  .register input:hover {
    background-color: rgb(35, 207, 199);
  }
  .register a:hover{
      color: rgb(35, 207, 199);
  }
  .register h4 {
    color: white;
    text-shadow: black 2px 3px 5px;
  }
  
  .register a {
    color: white;
    text-shadow: black 2px 3px 5px;
  }
  
